import { useState } from "react";
import { useAuthContext } from "@/app/providers/auth-provider";
import { TeamsService } from "../api/teams.service";
import { Team, CreateTeamDto, JoinTeamDto } from "@/entities/team/model/types";
import { useToast } from "@/shared/ui/use-toast";

export function useTeams() {
  const { token } = useAuthContext();
  const { toast } = useToast();
  const [isLoading, setIsLoading] = useState(false);
  const [myTeams, setMyTeams] = useState<Team[]>([]);

  const createTeam = async (data: CreateTeamDto) => {
    if (!token) {
      toast({
        title: "Ошибка",
        description: "Необходимо авторизоваться",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      const team = await TeamsService.createTeam(token, data);
      setMyTeams((prev) => [...prev, team]);
      toast({
        title: "Успешно",
        description: "Команда успешно создана",
      });
      return team;
    } catch (error) {
      toast({
        title: "Ошибка",
        description:
          error instanceof Error ? error.message : "Не удалось создать команду",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const joinTeam = async (data: JoinTeamDto) => {
    if (!token) {
      toast({
        title: "Ошибка",
        description: "Необходимо авторизоваться",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      const team = await TeamsService.joinTeam(token, data);
      setMyTeams((prev) => [...prev, team]);
      toast({
        title: "Успешно",
        description: "Вы присоединились к команде",
      });
      return team;
    } catch (error) {
      toast({
        title: "Ошибка",
        description:
          error instanceof Error
            ? error.message
            : "Не удалось присоединиться к команде",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const fetchMyTeams = async () => {
    if (!token) {
      toast({
        title: "Ошибка",
        description: "Необходимо авторизоваться",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      const teams = await TeamsService.getMyTeams(token);
      setMyTeams(teams);
      return teams;
    } catch (error) {
      toast({
        title: "Ошибка",
        description:
          error instanceof Error
            ? error.message
            : "Не удалось загрузить команды",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const getTeamById = async (teamId: number) => {
    if (!token) {
      toast({
        title: "Ошибка",
        description: "Необходимо авторизоваться",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      return await TeamsService.getTeamById(token, teamId);
    } catch (error) {
      toast({
        title: "Ошибка",
        description:
          error instanceof Error
            ? error.message
            : "Не удалось загрузить команду",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const getTeamByCode = async (code: string) => {
    if (!token) {
      toast({
        title: "Ошибка",
        description: "Необходимо авторизоваться",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      return await TeamsService.getTeamByCode(token, code);
    } catch (error) {
      toast({
        title: "Ошибка",
        description:
          error instanceof Error ? error.message : "Не удалось найти команду",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  const getTeamMembers = async (teamId: number) => {
    if (!token) {
      toast({
        title: "Ошибка",
        description: "Необходимо авторизоваться",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    try {
      return await TeamsService.getTeamMembers(token, teamId);
    } catch (error) {
      toast({
        title: "Ошибка",
        description:
          error instanceof Error
            ? error.message
            : "Не удалось загрузить участников команды",
        variant: "destructive",
      });
    } finally {
      setIsLoading(false);
    }
  };

  return {
    isLoading,
    myTeams,
    createTeam,
    joinTeam,
    fetchMyTeams,
    getTeamById,
    getTeamByCode,
    getTeamMembers,
  };
}
